import Vue from 'vue'
import Router from 'vue-router'
import start from '@/components/start'
import homes from '@/components/layout/home'
import main from '@/components/main'
import info from '@/components/info'
import mark from '@/components/mark'
import search from '@/components/search'
import login from '@/components/user/login/login'
import user from '@/components/user/user'
import reg from '@/components/user/reg/reg'
import cookie from '../assets/js/cookie.js'

Vue.use(Router);

const myRouter = new Router({
    // mode: 'history',
    routes: [
        {
            path: '/',
            name: 'start',
            component: start
        },
        {
            path: '/home',
            name: 'homes',
            component: homes,
            meta: {
                requiresAuth: true
            },
            children: [
                 {
                    path: '/home',
                    name: 'main',
                    component: main,
                    meta: {
                        requiresAuth: true
                    }
                },
                {
                    path: '/user',
                    name: 'user',
                    component: user
                },
                {
                    path: '/info',
                    name: 'info',
                    component: info
                },
                {
                    path: '/mark/:type',
                    name: 'mark',
                    component: mark
                },
                {
                    path: '/search',
                    name: 'search',
                    component: search
                }
            ]
        },
        {
            path: '/login',
            name: 'login',
            component: login
        },
        {
            path: '/reg',
            name: 'reg',
            component: reg
        }
    ]
})

myRouter.beforeEach((to, from, next) => {
    if(to.path === '/login')  { 
        next()  
    } else {     //进入的不是登录路由
        if(to.meta.requiresAuth && !cookie.getCookie('token')) {
            next({ path: '/login' })
        } else { 
            next() 
        }
    }
})

export default myRouter;